import React from 'react';
import { SmartLink } from './SmartLink';
import { PageItem } from '@uniformdev/common-client';

export class ItemLink extends React.Component<{ item: PageItem }> {
    render() {
        const lang = 'en'; //this.props.lang || 'en';

        const item = this.props.item;
        if (!item) throw new Error('no item');

        const href = (lang === 'en' ? '' : '/' + lang) + item.url;

        return <SmartLink href={href}>{this.props.children}</SmartLink>;
    }
}
